<template>
  <div>
    <el-card class="box-card">
      <div class="div">
        <el-input
          class="btn"
          placeholder="请输入内容"
          v-model="input"
          @keyup.enter="search()"
          clearable
        >
        </el-input>
        <el-button
          class="but"
          @click="search()"
          icon="el-icon-search"
        ></el-button>
        <el-button type="primary" @click="add()">添加商品</el-button>
      </div>
      <template>
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column type="index" label="#" width="50"> </el-table-column>
          <el-table-column prop="goods_name" label="商品名称" width="520">
          </el-table-column>
          <el-table-column prop="goods_price" label="商品价格(元)" width="100">
          </el-table-column>
          <el-table-column prop="goods_number" label="商品数量" width="100">
          </el-table-column>
          <el-table-column prop="goods_weight" label="商品重量" width="100">
          </el-table-column>
          <el-table-column prop="add_time" label="创建时间" width="200">
            <!-- <template slot-scope="scope">
              {{ scope.row.add_time }}
            </template> -->
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                class="b"
                @click="bj(scope.row)"
                type="primary"
                icon="el-icon-edit"
              ></el-button>
              <el-button
                class="b"
                @click="del(scope.row)"
                type="danger"
                icon="el-icon-delete"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pagenum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>

        <el-dialog title="编辑商品" :visible.sync="xg">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
                <el-form-item label="ID" prop='id'>
                        <el-input class="b" disabled v-model="ruleForm.id" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="商品名称" prop='name'>
                        <el-input class="b" v-model="ruleForm.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="商品价格" prop='price'>
                        <el-input class="b" v-model="ruleForm.price" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="商品数量" prop='num'>
                        <el-input class="b" v-model="ruleForm.num" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="商品重量" prop='weight'>
                        <el-input class="b" v-model="ruleForm.weight" autocomplete="off"></el-input>
                </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
                <el-button @click="xg = false">取 消</el-button>
                <el-button type="primary" @click="edit()">确 定</el-button>
        </div>
        </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      newtable:[],
      pagenum: 1,
      pagesize: 10,
      input: "",
      total: 0,
      xsearch: false,
      xg:false,
      ruleForm: {
        id: '',
        name: '',
        price: '',
        num: '',
        weight: '',
      },
      rules: {
        id: [
                {required: true,message: "请输入id",trigger: ["blur", "change"]},
        ],
        name: [
                {required: true,message: "请输入商品名称",trigger: ["blur", "change"]},
        ],
        price: [
                {required: true,message: "请输入商品价格",trigger: ["blur", "change"]},
        ],
        num: [
                {required: true,message: "请输入商品数量",trigger: ["blur", "change"]},
        ],
        weight: [
                {required: true,message: "请输入商品重量",trigger: ["blur", "change"]},
        ],
      }
    };
  },
  created() {},
  watch: {
    input(val) {
      if (val == "") {
        this.xsearch = false;
        this.table();
      }
      console.log(val);
    },
  },
  mounted() {
    this.table();
  },
  methods: {
    async table() {
      let res = "";
      if (this.xsearch) {
        res = await this.$http.get(
          `goods/?query=${this.input}&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
        );
        console.log(res);
      } else {
        res = await this.$http.get(
          `goods/?pagenum=${this.pagenum}&pagesize=${this.pagesize}`
        );
        console.log(res);
      }
      console.log(res.data.data.goods);
      this.tableData = res.data.data.goods;
      this.total = res.data.data.total;

      this.tableData.forEach((i,k) => {
        i.add_time=new Date(i.add_time).toLocaleString()
      })
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      val = this.pagesize = val;
      this.table();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      val = this.pagenum = val;
      this.table();
    },
    search() {
      this.xsearch = true;
      this.table();
    },
    add(){
      this.$router.push({
        path:'/list/addgoods',
      })
    },
    del(item) {
      console.log(item);
      console.log(item.goods_id);
      this.$http.delete(`goods/${item.goods_id}`).then((res) => {
        console.log(res);
        this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            this.table();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除",
            });
          });
      });
    },
    bj(item) {
        this.xg=true
        this.newtable=item
        console.log(this.newtable)

        this.ruleForm.id=this.newtable.goods_id
        this.ruleForm.name=this.newtable.goods_name
        this.ruleForm.price=this.newtable.goods_price
        this.ruleForm.num=this.newtable.goods_number
        this.ruleForm.weight=this.newtable.goods_weight
    },
    async edit(){
        let { data } = await this.$http.put('goods/'+this.newtable.goods_id,{
                goods_name:this.newtable.goods_name,
                goods_price:this.newtable.goods_price,
                goods_number:this.newtable.goods_number,
                goods_weight:this.newtable.goods_weight
        })
        console.log(data)
    }
  },
};
</script>

<style scoped lang="scss">
.box-card {
  width: 100%;
  line-height: 50px;
  margin-top: 20px;
  text-align: left;
}
.el-table {
  margin-top: 20px;
}
.div {
  width: 100%;
  height: 70px;
  line-height: 70px;
  .btn {
    width: 30%;
  }
  .but {
    border-left: none;
    background: #f5f7fa;
  }
}
.block {
  margin-top: 31px;
}
.b{
  padding: 10px 15px;
}
</style>
