<template>
  <div>
    <el-card class="box-card">
      <div class="div">
        <el-input
          class="btn"
          placeholder="请输入内容"
          v-model="input"
          @keyup.enter="search()"
          clearable
        >
        </el-input>
        <el-button
          class="but"
          @click="search()"
          icon="el-icon-search"
        ></el-button>
        <el-button type="primary" @click="add()">添加商品</el-button>
      </div>
      <el-table :data="tableData" stripe border style="width: 100%">
        <el-table-column type="index" label="#" width="50"> </el-table-column>
        <el-table-column prop="order_number" label="订单编号" width="310">
        </el-table-column>
        <el-table-column prop="order_price" label="订单价格" width="180">
        </el-table-column>
        <el-table-column prop="" label="是否付款" width="180">
          <template slot-scope="scope">
            <el-tag type="danger" effect="dark">
              {{ scope.row.is_send == "否" ? "未付款" : "已付款" }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="是否发货" width="180">
        </el-table-column>
        <el-table-column prop="create_time" label="下单时间" width="180">
        </el-table-column>
        <el-table-column prop="address" label="操作">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="修改订单地址" placement="top">
              <el-button @click="edit(scope.row)" type="primary" class="but" icon="el-icon-edit"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="物流信息" placement="top">
              <el-button @click="wl(scope.row)" type="success" class="but" icon="el-icon-location"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        :page-sizes="[10, 15, 20, 25]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      pagenum: 1,
      pagesize: 10,
      input:'',
      total:0,
    };
  },
  created() {},
  mounted() {
    this.table();
  },
  methods: {
    async table() {
      let { data } = await this.$http.get(
        `orders/?pagenum=${this.pagenum}&pagesize=${this.pagesize}`
      );
      console.log(data);
      this.tableData = data.data.goods;
      this.total = data.data.total;
      console.log(this.tableData);

      this.tableData.forEach((i, k) => {
        i.create_time = new Date(i.create_time).toLocaleString();
      });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      val = this.pagesize = val
      this.table()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      val = this.pagenum = val
      this.table()
    },
    search(){

    },
    add(){

    },
    edit(item){
      console.log(item)
    },
    wl(item){
      console.log(item)
    },
  },
  computed: {},
};
</script>

<style scoped lang="scss">
.box-card {
  width: 100%;
  line-height: 50px;
  margin-top: 20px;
  text-align: left;
}
.el-table {
  margin-top: 17px;
  margin-bottom: 20px;
}
.el-tag {
  padding: 0px 7px;
}
.but{
  padding: 8px 15px;
}
.div {
  width: 100%;
  height: 70px;
  line-height: 70px;
  .btn {
    width: 30%;
  }
  .but {
    height: 40px;
    border-left: none;
    background: #f5f7fa;
  }
}
</style>
