<template>
  <div>
    <el-card class="box-card">
      <el-alert
        title="注意：只允许为第三级分类设置相关参数！"
        type="warning"
        effect="dark"
        show-icon
      >
      </el-alert>
      <el-form :model="addForm" ref="addFormRef" label-width="130px">
        <el-form-item label="请选择商品分类：">
          <el-cascader
            v-model="fl"
            :options="shopList"
            expand-trigger="hover"
            :props="fllist"
            @change="handleChange"
          >
          </el-cascader>
        </el-form-item>
      </el-form>

      <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="动态参数" name='many'>
          <el-button :disabled='isbtn' type="primary" @click='omqh =true'>添加参数</el-button>
          <el-table :data="many" border style="width: 100%">
            <el-table-column type='expand' label=" " width="80">
              <template slot-scope='scope'>
                <el-tag class='tag' v-for='(i,k) in scope.row.attr_vals' 
                :key='k' closable @close='delclose(i,scope.row)'>
                  {{ i }}
                </el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type='index' label="#" width="80">
            </el-table-column>
            <el-table-column prop="attr_name" label="参数名称" width="630">
            </el-table-column>
            <el-table-column label="操作"> 
              <template slot-scope='scope'>
                <el-button class='but' type="primary" icon="el-icon-edit" @click='gai(scope.row)' >修改</el-button>
                <el-button class='but' type="danger" icon="el-icon-delete" @click='del(scope.row)' >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name='only'>
          <el-button :disabled='isbtn' type="primary" @click='omqh =true'>添加属性</el-button>
          
          <el-table :data="only" border style="width: 100%">
            <el-table-column type='expand' label=" " width="80">
              <template slot-scope='scope'>
                <el-tag class='tag' v-for='(i,k) in scope.row.attr_vals' 
                :key='k' closable  @close='delclose(i,scope.row)'>
                  {{ i }}
                </el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type='index' label="#" width="80">
            </el-table-column>
            <el-table-column prop="attr_name" label="属性名称" width="630">
            </el-table-column>
            <el-table-column label="操作"> 
              <template slot-scope='scope'>
                <el-button class='but' type="primary" icon="el-icon-edit" @click='gai(scope.row)' >修改</el-button>
                <el-button class='but' type="danger" icon="el-icon-delete" @click='del(scope.row)' >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>

      <!-- 添加参数 -->
      <el-dialog :title="'添加'+Text" :visible.sync="omqh" @close='addclose'>
        <el-form :model="addForm" :rules="rules" ref="addFormref" label-width="100px" class="demo-ruleForm">
          <el-form-item :label="Text" prop="name">
          <el-input v-model="addForm.name"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="omqh = false">取 消</el-button>
          <el-button type="primary" @click="add()">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 修改参数 -->
      <el-dialog :title="'修改'+Text" :visible.sync="xg">
        <el-form :model="addForm" :rules="rules" ref="addFormr" label-width="100px" class="demo-ruleForm">
          <el-form-item :label="Text" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="xg = false">取 消</el-button>
          <el-button type="primary" @click="edit()">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 静态
      only:[],
      // 动态
      many:[],
      omqh:false,
      xg:false,
      activeName:'many',
      addForm: {
        name:'',
        attr_name:'',
      },
      rules:{
        name: [
          { required: true, message: '参数名称不能为空', trigger: ['blur','change'] }
        ],
        attr_name: [
          { required: true, message: '参数名称不能为空', trigger: ['blur','change'] }
        ],
      },
      fl: [],
      shopList: [],
      editlist:[],
      fllist: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
      },
    };
  },
  created() {},
  mounted() {
    this.list();
    this.getparamsData()
  },
  methods: {
    async list() {
      let { data } = await this.$http.get("categories");
      console.log(data);
      this.shopList = data.data;
    },
    handleChange() {
      this.getparamsData()
    },
    handleClick(tab,event){
      this.getparamsData()
    },
    async getparamsData(){
      if(this.fl.length !== 3){
        this.fl=[]
        return
      }
      console.log(this.fl)

      let { data } = await this.$http.get(`categories/${this.fl[2]}/attributes/`,{
        params:{ sel: this.activeName}
      })
      console.log(data)
      data.data.forEach((i,k) => {
        i.attr_vals = i.attr_vals?i.attr_vals.split(","):[]
        i.inputVisible=false
        i.inputValue=''
      })
      if(this.activeName === 'only'){
        this.only=data.data
      }else{
        this.many=data.data
      }
    },
    gai(item){
      this.xg=true
      this.editlist=item
      console.log(this.editlist)
      this.addForm.attr_name=this.editlist.attr_name
    },
    async edit(){
      let { data } = await this.$http.put(`categories/${this.isflId}/attributes/${this.editlist.attr_id}`,{
        attr_name:this.addForm.attr_name,
        attr_sel:this.activeName
      })
      console.log(data)
      this.$message({
        type: "success",
        duration: 2000,
        message: "修改参数成功",
      });
      this.getparamsData()
      this.xg=false
    },
    async del(item){
      console.log(item.attr_id)
      let {data:res} = await this.$http.delete(`categories/${this.isflId}/attributes/${item.attr_id}`)
      console.log(res)
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
      .then(() => {
        this.$message({
          type: "success",
          message: "删除成功!",
        });
        // this.catelist.splice(item.attr_id, 1);
        this.getparamsData();
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除",
        });
      });
    },
    add(){
      this.$refs.addFormref.validate(async vali => {
        if(!vali) return
          let {data} = await this.$http.post(`categories/${this.isflId}/attributes`,{ 
            attr_name:this.addForm.name,
            attr_sel:this.activeName,
          })
          console.log(data)
          this.$message({
            type: "success",
            duration: 2000,
            message: "添加参数成功",
          });
          this.list()
          this.getparamsData()
          this.omqh=false
      })
    },
    addclose(){
      this.$refs.addFormref.resetFields()
    },
    showInput(row){
      console.log(row.inputVisible=true)
      row.inputVisible=true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    async handleInputConfirm(row){
      if(row.inputValue.trim().length === 0){
        this.inputValue=''
        row.inputVisible=false
        return
      }

      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false

      this.saveAttr(row)
    },

    async saveAttr(row){
      let { data } = await this.$http.put(`categories/${this.isflId}/attributes/${row.attr_id}`,{
        attr_name:row.attr_name,
        attr_sel:row.attr_sel,
        attr_vals:row.attr_vals.join(',')
      })
      console.log(data)

      this.$message({
        type: "success",
        duration: 2000,
        message: "更新参数成功",
      });
    },
    delclose(i,row){
      row.attr_vals.splice(i,1)
      this.saveAttr(row)
    }
  },
  
  computed: {
    isbtn(){
      if(this.fl.length !== 3){
        return true
      }
      return false
    },
    Text(){
      if(this.activeName === 'many'){
        return '动态参数'
      }
      return '静态属性'
    },
    isflId(){
      if(this.fl.length === 3){
        return this.fl[2]
      }
      return null
    }
  }
};
</script>

<style scoped lang="scss">
.box-card {
  width: 100%;
  line-height: 50px;
  margin-top: 20px;
  text-align: left;
}
.el-table {
  margin-top: 17px;
}
.el-alert {
  height: 40px;
}
.el-form {
  margin-top: 30px;
}
.el-tabs {
  line-height: 36px;
  .el-button {
    padding: 8px 10px;
    font-size: 13px;
  }
}
.tag{
  margin: 5px 10px;
}
.input-new-tag{
  width: 120px;

}
</style>
