<template>
    <div class="login">
        <p class="load" v-show="$store.state.islod">
            <img src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1744558263,856016303&fm=26&gp=0.jpg" alt="">
        </p>
        <div class="login_dl">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="用户名" prop="val">
                        <el-input type="text" v-model="ruleForm.val" prefix-icon='el-icon-user' placeholder="请输入用户名"></el-input>
                    </el-form-item>
                </el-form>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="密码" prop="pwd">
                        <el-input type="password" v-model="ruleForm.pwd" prefix-icon='el-icon-lock' placeholder="请输入用户名"></el-input>
                    </el-form-item>
                </el-form>
            
            <button class="dl" @click="dl()" title="有功能哦">登录</button>
            <button class="cz" @click="cz()" title="有功能哦">重置</button>
        </div>
        
        <div class="img_vue">
            <img src="../assets/logo.png" alt="">
        </div>
    </div>
</template>

<script>
import axios from 'axios'
// axios.defaults.withCredentials=true;//让ajax携带cookie
export default {
    name: 'login',
    data() {
        return {
            val:'',
            pwd:'',
            rules: {
                val: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                pwd: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
                ],
            },
            ruleForm: {
                val:'',
                pwd:'',
            },
        };
    },
    created() {

    },
    mounted() {
        // this.ruleForm.val='admin'
        // this.ruleForm.pwd='123456'
        this.ruleForm.val=''
        this.ruleForm.pwd=''
    },
    methods: {
        dl(){
            this.$http.post('login',{username:this.ruleForm.val,password:this.ruleForm.pwd}).then((res) => {
                console.log(res)
                console.log(res.data)
                console.log(res.data.meta)
                if(this.ruleForm.val==''||this.ruleForm.pwd==''){
                    alert('请输入用户名或者密码')
                    return false
                }else{
                    if(res.data.meta.msg=='登录成功'){
                        this.$message({
                            message:res.data.meta.msg,
                            duration:2000,
                            type:'success',
                        })
                        window.sessionStorage.setItem('token',res.data.data.token)
                        this.$router.push('/list/welcome')
                    }else{
                        this.$message({
                            message:res.data.meta.msg,
                            duration:2000,
                            type:'error'
                        })
                    }
                }
            })
            
        },
        onSubmit(){
            
        },
        cz(){
            // this.ruleForm.val='admin'
            // this.ruleForm.pwd='123456'
            this.ruleForm.val=''
            this.ruleForm.pwd=''
        }
    }
};
</script>

<style scoped lang='scss'>
.login{
    width: 100%;
    height: 100%;
    background: #2B4B6B;
}
.login_dl{
    width: 500px;
    height: 350px;
    background: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-240px,-125px);
    padding: 120px 20px;
    box-sizing: border-box;
    border: 10px solid #FFFFFF;
    box-shadow: 0px 0px 10px 1px #ddd;
    border-radius: 5px;
    .van-field{
        border: 1px solid #eee;
        margin: 7px 0px;
    }
    .pwd{
        float: left;
        margin-top: 20px;
    }
    .cz{
        color: white;
        background: #909399;
        border-color: #909399;
        margin: 20px 10px;
        padding: 12px 20px;
        float: right;
        font-size: 12px;
        border-radius: 5px;
        outline: none;
        cursor: pointer;
    }
    .dl{
        color: white;
        background: #409eff;
        border-color: #409eff;
        margin: 20px 0px;
        margin-left: 283px;
        padding: 12px 20px;
        float: left;
        font-size: 12px;
        border-radius: 5px;
        outline: none;
        cursor: pointer;
    }
}
.img_vue{
    width: 152px;
    height: 152px;
    background: #eee;
    text-align: center;
    line-height: 150px;
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 5px 0px;
    box-sizing: border-box;
    transform: translate(-62px,-200px);
    border-radius: 50%;
    border: 10px solid #FFFFFF;
    box-shadow: 0px 0px 15px 1px #ddd;
    overflow: hidden;
    img{
        width: 130px;
        height: 130px;
    }
}
.load{
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.5);
    // background: white;
    position: absolute;
    z-index: 10000;
    img{
        width: 250px;
        height: 200px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-80px,-100px);
        z-index: 1000;
    }
}
</style>
