<template>
  <div>
    <el-card class="box-card">
      <el-button type="primary" @click="add = true">添加角色</el-button>
      <el-table :data="tableData" border stripe style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-row
                :class="['dbbottom', index === 0 ? 'dbtop' : '']"
                v-for="(item, index) in scope.row.children"
                :key="index"
              >
                <!-- <el-col :span="24"> -->
                <el-col :span="5">
                  <el-tag
                    closable
                    @close="removeById(scope.row, item.id)"
                    :disable-transitions="false"
                    >{{ item.authName }}</el-tag
                  >
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <el-col :span="19">
                  <el-row
                    :class="[i === 0 ? '' : 'dbtop']"
                    v-for="(it, i) in item.children"
                    :key="i"
                  >
                    <el-col :span="6">
                      <el-tag
                        closable
                        type="success"
                        @close="removeById(scope.row, it.id)"
                        :disable-transitions="false"
                        >{{ it.authName }}</el-tag
                      >
                      <i class="el-icon-caret-right"></i>
                    </el-col>
                    <el-col :span="18">
                      <el-tag
                        closable
                        @close="removeById(scope.row, t.id)"
                        type="warning"
                        :disable-transitions="false"
                        v-for="(t, d) in it.children"
                        :key="d"
                        >{{ t.authName }}</el-tag
                      >
                    </el-col>
                  </el-row>
                </el-col>
                <!-- </el-col> -->
              </el-row>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#" width="80"> </el-table-column>
        <el-table-column prop="roleName" label="角色名称" width="400">
        </el-table-column>
        <el-table-column prop="roleDesc" label="角色描述" width="400">
        </el-table-column>
        <el-table-column prop="address" label="操作">
          <!-- <template slot-scope="scope">
                        {{ scope.row }}
                </template> -->
          <template slot-scope="scope">
            <el-button
              class="btn"
              @click="gai(scope.row)"
              type="primary"
              icon="el-icon-edit"
              >编辑</el-button
            >
            <el-button
              class="btn"
              @click="del(scope.row)"
              type="danger"
              icon="el-icon-delete"
              >删除</el-button
            >
            <el-button
              class="btn"
              type="warning"
              icon="el-icon-s-tools"
              @click="fen(scope.row)"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <!-- 添加角色 -->
      <el-dialog title="添加角色" :visible.sync="add">
        <el-form>
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="角色名称" prop="name">
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="角色描述" prop="ms">
              <el-input v-model="ruleForm.ms"></el-input>
            </el-form-item>
          </el-form>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="add = false">取 消</el-button>
          <el-button type="primary" @click="tj()">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 编辑角色 -->
      <el-dialog title="修改角色" :visible.sync="xg">
        <el-form>
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="角色名称" prop="xname">
              <el-input v-model="ruleForm.xname"></el-input>
            </el-form-item>
            <el-form-item label="角色描述" prop="xms">
              <el-input v-model="ruleForm.xms"></el-input>
            </el-form-item>
          </el-form>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="xg = false">取 消</el-button>
          <el-button type="primary" @click="edit()">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 分配权限 -->

      <el-dialog
        title="分配权限"
        :visible.sync="fp"
        width="30%"
        @close="setkeys"
      >
        <el-tree
          :default-checked-keys="defkeys"
          default-expand-all
          show-checkbox
          node-key="id"
          :data="rightsList"
          :props="treeProps"
          ref="treefp"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="fp = false">取 消</el-button>
          <el-button type="primary" @click="fpjs()">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      rightsList: [],
      defkeys: [],
      add: false,
      xg: false,
      fp: false,
      i: "",
      gb: [],
      treeProps: {
        children: "children",
        label: "authName",
      },
      ruleForm: {
        name: "",
        ms: "",
        xname: "",
        xms: "",
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入角色名称",
            trigger: ["blur", "change"],
          },
          {
            min: 2,
            max: 8,
            message: "角色名称长度在2~8个字符之间",
            trigger: ["blur", "change"],
          },
        ],
        ms: [
          {
            required: true,
            message: "请输入角色的描述",
            trigger: ["blur", "change"],
          },
        ],
        xname: [
          {
            required: true,
            message: "请输入角色名称",
            trigger: ["blur", "change"],
          },
          {
            min: 2,
            max: 8,
            message: "角色名称长度在2~8个字符之间",
            trigger: ["blur", "change"],
          },
        ],
        xms: [
          {
            required: true,
            message: "请输入角色的描述",
            trigger: ["blur", "change"],
          },
        ],
      },
    };
  },
  watch: {
    add(val) {
      if (val != "") {
        this.ruleForm = {
          name: "",
          ms: "",
        };
      }
      if (val == "") {
        this.rules = {
          name: [
            {
              required: false,
              message: "请输入角色名称",
              trigger: ["blur", "change"],
            },
            {
              min: 2,
              max: 8,
              message: "角色名称长度在2~8个字符之间",
              trigger: ["blur", "change"],
            },
          ],
          ms: [
            {
              required: false,
              message: "请输入角色的描述",
              trigger: ["blur", "change"],
            },
          ],
        };
      }
    },
  },
  created() {},
  mounted() {
    this.table();
  },
  methods: {
    table() {
      this.$http.get("roles").then((res) => {
        // console.log(res);
        this.tableData = res.data.data;
        // console.log(this.tableData);
        console.log(res.data.data);
      });
    },
    tj() {
      this.$http
        .post("roles", {
          roleName: this.ruleForm.name,
          roleDesc: this.ruleForm.ms,
        })
        .then((res) => {
          console.log(res);
          if (res.data.meta.msg == "创建成功") {
            this.$message({
              type: "success",
              duration: 2000,
              message: "创建角色成功",
            });
            this.add = false;
            this.table();
          } else {
            this.$message({
              type: "info",
              duration: 2000,
              message: "取消创建",
            });
            this.add = false;
          }
        });
    },
    del(item) {
      console.log(item);
      this.$http.delete("roles/" + item.id).then((res) => {
        console.log(res);
        this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            this.tableData.splice(item.id, 1);
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            this.table();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除",
            });
          });
      });
    },
    gai(item) {
      this.xg = true;
      console.log(item);
      this.gb = item;

      this.ruleForm.xname = this.gb.roleName;
      this.ruleForm.xms = this.gb.roleDesc;
    },
    edit() {
      this.$http
        .put("roles/" + this.gb.id, {
          roleName: this.ruleForm.xname,
          roleDesc: this.ruleForm.xms,
        })
        .then((res) => {
          console.log(res);
          this.$message({
            type: "success",
            duration: 2000,
            message: "更新角色成功",
          });
          this.xg = false;
          this.table();
        });
    },
    removeById(item, id) {
      console.log(item, id);
      this.$http.delete(`roles/${item.id}/rights/${id}`).then((res) => {
        console.log(res);
        this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            this.tableData.splice(item.id, 1);
            this.$message({
              type: "success",
              message: res.data.meta.msg,
            });
            this.table();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除",
            });
          });
      });
    },
    fen(role) {
      this.$http.get("rights/tree").then((res) => {
        console.log(res);
        this.rightsList = res.data.data;
      });
      this.i = role.id;
      // this.defkeys=[]
      this.leafKeys(role, this.defkeys);
      this.fp = true;
    },
    leafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id);
      }

      node.children.forEach((item) => {
        this.leafKeys(item, arr);
      });
    },
    async fpjs() {
      let keys = [
        ...this.$refs.treefp.getCheckedKeys(),
        ...this.$refs.treefp.getHalfCheckedKeys(),
      ];

      let idstr = keys.join(",");

      let { data: res } = await this.$http.post(`roles/${this.i}/rights`, {
        rids: idstr,
      });

      console.log(res);
      if (res.meta.status != 200) {
        this.$message({
          type: "success",
          duration: 2000,
          message: "权限分配失败",
        });
      }

      this.$message({
        type: "success",
        duration: 2000,
        message: "权限分配成功",
      });

      this.table();
      this.fp = false;
    },
    setkeys() {
      this.defkeys = [];
    },
  },
};
</script>

<style scoped lang="scss">
.box-card {
  width: 100%;
  line-height: 50px;
  margin-top: 20px;
  text-align: left;
}
.el-table {
  margin-top: 20px;
}
.demo-table-expand {
  font-size: 0;
  margin-left: -10px;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 33%;
  border: 1px solid #efefef;
  text-align: center;
}
.btn {
  // width: 110px;
  // height: 40px;
  padding: 10px 10px;
  text-indent: -1px;
  font-size: 13px;
}
.but1,
.but2,
.but3 {
  padding: 9px 9px;
  font-size: 13px;
  margin-left: 15px;
}
.el-row {
  border: 1px solid #eee;
  padding: 0px 11px;
  box-sizing: border-box;
}
</style>
