<template>
    <div>
        <el-card class="box-card">
             <el-alert
                title="添加商品信息"
                type="info"
                center
                show-icon>
            </el-alert>
            <el-steps :active="activeIndex - 0" finish-status="success" :space="200" align-center>
                <el-step title="基本信息"></el-step>
                <el-step title="商品参数"></el-step>
                <el-step title="商品属性"></el-step>
                <el-step title="商品图片"></el-step>
                <el-step title="商品内容"></el-step>
                <el-step title="完成"></el-step>
            </el-steps>

            <el-form :model="addForm" :rules="addFormrules" ref="addFormRef" label-width="100px" label-position='top'>
                <el-tabs class="tabs" v-model="activeIndex" :tab-position="'left'"
                :before-leave='beforeTab' @tab-click='tabClicked'>
                    <el-tab-pane label="基本信息" name='0'>
                        <el-form-item label="商品名称" prop="name">
                            <el-input v-model="addForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="商品价格" type='number' prop="price">
                            <el-input v-model="addForm.price"></el-input>
                        </el-form-item>
                        <el-form-item label="商品重量" type='number' prop="weight">
                            <el-input v-model="addForm.weight"></el-input>
                        </el-form-item>
                        <el-form-item label="商品数量" type='number' prop="num">
                            <el-input v-model="addForm.num"></el-input>
                        </el-form-item>
                        <el-form-item label="商品分类" prop="fl">
                            <el-cascader
                            v-model="addForm.fl"
                            :options="shopList"
                            expand-trigger='hover'
                            :props="fllist"
                            @change="handleChange"></el-cascader>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品参数" name='1'>
                        <el-form-item :label="item.attr_name" v-for="(item,key) in cslist" 
                        :key="key">
                            <!-- {{ item.attr_name }} -->
                            <el-checkbox-group v-model="item.attr_vals">
                                <el-checkbox border :label="i" v-for="(i,k) in item.attr_vals" 
                                :key="k">

                                </el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品属性" name='2'>
                        <el-form-item :label="item.attr_name" v-for="item in sxlist" 
                        :key="item.attr_id">
                            <el-input v-model="item.attr_vals"></el-input>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品图片" name='3'>
                        <el-upload
                            :action="scurl"
                            list-type="picture-card"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove"
                            :headers='headersObj'
                            :on-success='sctp'>
                            <i class="el-icon-plus"></i>
                            </el-upload>
                            <el-dialog :visible.sync="dialogVisible">
                            <img width="100%" :src="dialogImageUrl" alt="">
                        </el-dialog>
                    </el-tab-pane>
                    <el-tab-pane label="商品内容" name='4'>
                        <el-button type="primary" @click="tjsp()">添加商品</el-button>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
    </div>
</template>
<script>
export default {
    data() {
        return {
            activeIndex:'0',
            dialogImageUrl: '',
            dialogVisible: false,
            headersObj:{
                authorization:window.sessionStorage.getItem('token')
            },
            addForm:{
                name:'',
                price:0,
                weight:0,
                num:0,
                fl:[],
                pics:[],
            },
            addFormrules:{
                name: [
                    { required: true, message: '请输入商品名称', trigger: ['blur','change'] },
                ],
                price: [
                    { required: true, message: '请输入商品价格', trigger: ['blur','change'] },
                ],
                weight: [
                    { required: true, message: '请输入商品重量', trigger: ['blur','change'] },
                ],
                num: [
                    { required: true, message: '请输入商品数量', trigger: ['blur','change'] },
                ],
                fl: [
                    { required: true, message: '请输入商品分类', trigger: ['blur','change'] },
                ],
            },
            shopList:[],
            cslist:[],
            sxlist:[],
            fllist:{
                children:'children',
                value:'cat_id',
                label:'cat_name',
            },
            scimg:'http://127.0.0.1:8888/api/private/v1/upload',
            scurl:'https://www.liulongbin.top:8888/api/private/v1/upload'
        };
    },
    created() {
        
    },
    mounted() {
        this.list()
    },
    methods: {
        handleRemove(file, fileList) {
            console.log(file, fileList);
            let filepath={
                pic: file.response.data.tmp_path
            }

            let i = this.addForm.pics.findIndex(m => {
                return m.pics === filepath
            })

            this.addForm.pics.splice(i,1)
            console.log(this.addForm)
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        async list(){
            this.shopList
            let { data } = await this.$http.get('categories')
            console.log(data)
            this.shopList=data.data
        },
        handleChange(){
            if(this.addForm.fl.length !== 3){
                this.addForm.fl=[]
                this.$message({
                    type: "warning",
                    duration: 2000,
                    message: "只能选择三级商品分类!",
                });
            }
            console.log(this.addForm.fl[2])
        },
        beforeTab(activeName,oldActiveName){
            // console.log('即将进入的页面索引是'+activeName)
            // console.log('即将离开的页面索引是'+oldActiveName)
            if(oldActiveName === '0' && this.addForm.fl.length !== 3){
                this.$message({
                    type: "error",
                    duration: 2000,
                    message: "请先选择商品分类!",
                });
                return false
            }
        },
        async tabClicked(){
            if(this.activeIndex === '1'){
                let { data:res } = await this.$http.get(`categories/${this.cateId}/attributes`,{
                    params:{ sel: 'many' }
                })
                console.log(res)
                res.data.forEach((i,k) => {
                    i.attr_vals=i.attr_vals.length === 0?[]:i.attr_vals.split(',')
                })
                this.cslist=res.data
            }else if(this.activeIndex === '2'){
                let { data:res } = await this.$http.get(`categories/${this.cateId}/attributes`,{
                    params:{ sel: 'only' }
                })
                console.log(res)
                this.sxlist=res.data
            }
        },
        sctp(response){
            console.log(response)

            let picInfo={
                pic: response.data.tmp_path
            }

            this.addForm.pics.push(picInfo)
            console.log(this.addForm)
        },
        tjsp(){
            this.$refs.addFormRef.validate(async vali =>{
                if(!vali){
                    return
                }
                console.log(vali)
                // let {data:res} = await this.$http.post(`goods/`,{
                //     goods_name:this.addForm.name,
                //     goods_cat:.join(','),
                //     goods_price:this.addForm.price,
                //     goods_number:this.addForm.num,
                //     goods_weight:this.addForm.weight,
                //     pics:this.addForm.pics,
                // })
                // console.log(data)
            })
        }
    },
    computed: {
        cateId(){
            if(this.addForm.fl.length === 3){
                return this.addForm.fl[2]
            }
            return null
        }
    }
};
</script>

<style scoped lang='scss'>
.box-card {
  width: 100%;
  line-height: 33px;
  margin-top: 20px;
  text-align: left;
}
.el-table {
  margin-top: 20px;
}
.el-alert{
    line-height: 30px;}
.el-steps{
    margin-top: 20px;
    
}
.el-step__title .is-process{
    font-size: 13px;
}
.tabs{
    margin-top: 30px;
}
</style>
