<template>
  <div>
    <el-card class="box-card">
      <el-button class='b' @click='tj()' type="primary">添加分类</el-button>
      <tree-table :data='catelist' :columns='columns'
      :selection-type='false' show-index index-text='#'
      :expand-type='false' border :show-row-hover='false'>
        <template slot='isok' slot-scope='scope'>
          <i class="el-icon-success" style='color: #39CE39;' v-if='scope.row.cat_deleted === false'></i>
          <i class="el-icon-error" style='color: #39CE39;' v-else></i>
        </template>
        <template slot='px' slot-scope='scope'>
            <el-tag
              effect="dark"
              size='mini'
              v-show="scope.row.cat_level==0">
              一级
            </el-tag>
            <el-tag
              type='success'
              effect="dark"
              size='mini'
              v-show="scope.row.cat_level==1">
              二级
            </el-tag>
            <el-tag
              type='warning'
              effect="dark"
              size='mini'
              v-show="scope.row.cat_level==2">
              三级
            </el-tag>
        </template>
        <template slot='cz' slot-scope='scope'>
          <el-button class='but' type="primary" icon="el-icon-edit" @click='gai(scope.row)' >编辑</el-button>
          <el-button class='but' type="danger" icon="el-icon-delete" @click='del(scope.row)' >删除</el-button>
        </template>
      </tree-table>

      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
      
      <!-- 添加商品分类 -->
      <el-dialog title="添加商品分类" :visible.sync="tjfl" 
      @close='catechange'>
        <el-form :model="addform" :rules="rules" ref="addformref" label-width="100px" class="demo-ruleForm">
          <el-form-item label="分类名称" prop="cat_name">
            <el-input v-model="addform.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="商品分类">
              <el-cascader
              clearable
              change-on-select
              v-model="fl"
              :options="xcatelist"
              expand-trigger='herver'
              :props="fllist"
              @change="handleChange"></el-cascader>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="tjfl = false">取 消</el-button>
          <el-button type="primary" @click="add()">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 编辑商品分类 -->
      <el-dialog title="编辑商品分类" :visible.sync="xg">
        <el-form :model="addform" :rules="rules" ref="addformr" label-width="100px" class="demo-ruleForm">
          <el-form-item label="分类名称" prop="name">
            <el-input v-model="addform.name"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="xg = false">取 消</el-button>
          <el-button type="primary" @click="edit()">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tjfl:false,
      xg:false,
      addform:{
        cat_name:'',
        name:'',
        cat_pid:0,
        cat_level:0,
      },
      fl:[],
      rules: {
        cat_name: [
          {required: true,message: "分类名称不能为空",trigger: ["blur", "change"]},
        ],
        name: [
          {required: true,message: "分类名称不能为空",trigger: ["blur", "change"]},
        ],
      },
      queryInfo:{
        type:3,
        pagenum:1,
        pagesize:5,
      },
      Info:{
        type:2,
        pagenum:1,
        pagesize:5,
      },
      catelist:[],
      xcatelist:[],
      flname:[],
      fllist:{
        value:'cat_id',
        label:'cat_name',
        checkStrictly: true,
        children:'children'
      },
      total:0,
      columns:[
        {
          label:'分类名称',
          prop:'cat_name',
        },
        {
          label:'是否有效',
          type:'template',
          template:'isok',
        },
        {
          label:'排序',
          type:'template',
          template:'px',
        },
        {
          label:'操作',
          type:'template',
          template:'cz',
        }
      ]
    };
  },
  created() {

  },
  mounted() {
    this.cateList()
    this.newcateList()
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      val = this.queryInfo.pagesize = val;
      this.cateList()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      val = this.queryInfo.pagenum = val;
      this.cateList()
    },
    async cateList(){
      let {data} = await this.$http.get('categories',{
        params:this.queryInfo
      })
      console.log(data)
      this.catelist=data.data.result
      this.total=data.data.total
    },
    tj(){
      this.newcateList()
      this.tjfl=true
    },
    async newcateList(){
        let {data:res} = await this.$http.get('categories',{
        params:{ type: 2 }
      })
      console.log(res)
      this.xcatelist=res.data
      console.log(this.xcatelist)
    },
    add(){
      this.$refs.addformref.validate(async vali => {
        let { data } = await this.$http.post('categories',this.addform)
        console.log(data)
        this.$message({
            type: "success",
            duration: 2000,
            message: "添加商品分类成功!",
        });
        this.cateList()
        this.tjfl=false
      })
    },
    handleChange(){
      console.log(this.fl)
      if(this.fl.length > 0){
        // 父级分类的ID(商品分类)
        this.addform.cat_pid = this.fl[this.fl.length - 1]

        // 当前分类的等级赋值 1，2，3
        this.addform.cat_level = this.fl.length
        return
      }else{
        this.addform.cat_pid = 0

        // 当前分类的等级赋值 1，2，3
        this.addform.cat_level = 0
      }
    },
    catechange(){
      this.$refs.addformref.resetFields()
      this.fl=[]
      this.addform.cat_level=0
      this.addform.cat_pid=0
    },
    async del(item){
      let {data:res} = await this.$http.delete(`categories/`+item.cat_id)
      console.log(res)
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
      .then(() => {
        this.$message({
          type: "success",
          message: "删除成功!",
        });
        this.catelist.splice(item.cat_id, 1);
        this.cateList();
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消删除",
        });
      });
    },
    gai(item){
      this.xg=true
      console.log(item)
      this.flname=item
      this.addform.name=this.flname.cat_name
    },
    async edit(){
      let {data:res} = await this.$http.put('categories/'+this.flname.cat_id,{
        cat_name:this.addform.name
      })
      console.log(res)
      this.$message({
        type: "success",
        duration: 2000,
        message: "更新商品分类成功",
      });
    }
  },
};
</script>

<style scoped lang="scss">
.box-card {
  width: 100%;
  line-height: 50px;
  margin-top: 20px;
  text-align: left;
}
.but{
  padding: 7px 13px;
}
.block{
  margin-top: 20px;
}
.btn{
  padding: 4px 5px;
  font-size: 10px;
  margin-left: 11px;
}
.b{
  margin-bottom: 13px;
}
.el-cascader{
  width: 100%;
}
</style>
