<template>
    <div class="list">
        <p class="load" v-show="$store.state.islod">
            <img src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1744558263,856016303&fm=26&gp=0.jpg" alt="">
        </p>
        <el-container>
            <el-header class="head">
                <!-- <div class="head"> -->
                    <img src="../assets/img/155.jpg" alt="">
                    <p>电商后台管理系统</p>
                    <el-button type="info" @click="tc()">退出</el-button>
                <!-- </div> -->
            </el-header>
            <el-container>
                <el-aside :width="isCollapse?'64px':'203px'" :translate="isCollapse?'10s':'10s'" :collapse="isCollapse">
                    <el-menu
                    :collapse-transition='false'
                    unique-opened
                    text-color="#fff" 
                    background-color="#333744" 
                    class="el-menu-vertical-demo" 
                    @open="handleOpen" 
                    @close="handleClose" 
                    :collapse="isCollapse"
                    router
                    :default-active='activePath'>
                        <div class="top-bar" :collapse="isCollapse" @click="isCollapse = !isCollapse">|||</div>
                        <el-submenu :index="cid[key]" v-for="(item,key) in leftList" :key='key'>
                            <template slot="title">
                            <i style="fontSize: 15px;" :class="icon[item.id]"></i>
                            <span style="margin-right: 100px;fontSize: 13px;">{{ item.authName }}</span>
                            </template>
                            <el-menu-item-group style="padding: 0px 0px;">
                                <el-menu-item v-for="(i,k) in item.children" :key='k' :index="'/list/'+i.path" @click="yh(item.authName,i.authName,i.path)">
                                    <i class="el-icon-menu"></i>
                                    <span style="fontSize: 13px;">{{ i.authName }}</span>
                                </el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>
                    <el-breadcrumb v-if="activePath==null|activePath==''">
                        <el-breadcrumb-item>首页</el-breadcrumb-item>
                    </el-breadcrumb>
                    <el-breadcrumb v-else-if="activePath=='/list/addgoods'">
                        <el-breadcrumb-item :to="{ path: '/list/welcome' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>{{ uname }}</el-breadcrumb-item>
                        <el-breadcrumb-item>添加商品</el-breadcrumb-item>
                    </el-breadcrumb>
                    <el-breadcrumb v-else>
                        <el-breadcrumb-item :to="{ path: '/list/welcome' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>{{ uname }}</el-breadcrumb-item>
                        <el-breadcrumb-item>{{ name }}</el-breadcrumb-item>
                    </el-breadcrumb>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import $ from 'jquery'
export default {
    name: 'list',
    data() {
        return {
            uname:sessionStorage.getItem('uname'),
            name:sessionStorage.getItem('name'),
            flag: true,
            isCollapse: false,
            leftList:[],
            activePath:'',
            icon:{
                "125":'el-icon-user',
                "103":'el-icon-takeaway-box',
                "101":'el-icon-shopping-bag-1',
                "102":'el-icon-tickets',
                "145":'el-icon-s-platform',
            },
            cid:{
                "0":'1',
                "1":'2',
                "2":'3',
                "3":'4',
                "4":'5',
            },
            // xid:{
            //     "110":'1',
            //     "111":'2',
            //     "112":'3',
            //     "104":'4',
            //     "115":'5',
            //     "121":'6',
            //     "101":'7',
            //     "107":'8',
            //     "146":'9',
            // },
            
        };
    },
    created() {
        let ly = window.sessionStorage.getItem('activepath')
        this.activePath = ly
    },
    mounted() {
        this.left()
    },
    methods: {
        tc(){
            this.$confirm('您确定要退出登录吗?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                this.$message({
                    type: 'success',
                    duration:2000,
                    message: '退出成功!'
                });
                this.$router.push('/login')
                window.sessionStorage.removeItem('token')
                window.sessionStorage.removeItem('uname')
                window.sessionStorage.removeItem('name')
                window.sessionStorage.removeItem('activepath')
                }).catch(() => {
                this.$message({
                    type: 'info',
                    duration:2000,
                    message: '已取消退出'
                });
            });
        },
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        yh(uname,name,path){

            window.sessionStorage.setItem('activepath','/list/'+path)
            this.activePath = '/list/'+path

            window.sessionStorage.setItem('uname',uname)
            window.sessionStorage.setItem('name',name)
            this.uname = sessionStorage.getItem('uname')
            this.name = sessionStorage.getItem('name')

            // console.log(uname,name)
            // this.$router.push({
            //     path:'/list/'+path,
            //     query:{
            //         uname:uname,
            //         name:name
            //     }
            // })
        },
        left(){
            this.$http.get('menus').then((res) => {
                // console.log(res)
                // console.log(res.data.data)
                this.leftList=res.data.data
                // console.log(this.leftList)
            })
        }
    },
    watch: {
        $route(to,from){
            if(to.path == '/list/welcome'){
                this.activePath = ''
            }else{
                this.activePath = to.path
            }
            console.log(to,from)
            window.sessionStorage.setItem('activepath',this.activePath)
            this.activePath = to.path
        },
    }
};
</script>

<style scoped lang='scss'>
