    background-color: #373D41;
    color: white;
    height: 80%;
    line-height: 80px;
    display: flex;
    align-items: center;
    padding: 0px 10px;
    box-sizing: border-box;
}
.el-aside {
    background-color: white;
    color: #333;
    text-align: center;
}

.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
}

.el-container {
    height: 100%;
    margin-bottom: 40px;
}
// .el-menu{
//     .el-menu-item-group{
//         .el-menu-item-group__title{
//             padding: 0px;
//         }
//     }
// }

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
    line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
    line-height: 320px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 202px;
    min-height: 400px;
  }
.list{
    width: 100%;
    height: 100%;
}
