//     width: 100%;
//     height: 80px;
//     background: #373D41;
//     color: white;
//     display: flex;
//     align-items: center;
//     padding: 0px 10px;
//     box-sizing: border-box;
    img{
        width: 60px;
        height: 60px;
        border-radius: 50px;
    }
    p{
        // width: 20%;1
        height: 50px;
        line-height: 50px;
        margin-left: 7px;
        font-size: 20px;
        font-weight: lighter;
    }
    .el-button{
        width: 70px;
        height: 40px;
        margin-left: 72%;
    }
// }
// .el-menu-vertical-demo{
//     width: 212px;
//     height: 100%;
//     padding: 0px 10px;
//     box-sizing: border-box;
// }
// .tac{
//    height: 100%;
// }
// .col{
//     height: 100%;
// }
.top-bar{
    background: #4a5064;
    color: white;
    font-size: 12px;
    text-align: center;
    line-height: 22px;
    cursor: pointer;
    letter-spacing: .12em;
}
.el-menu{
    height: 202px;
    height: 100%;
}
.load{
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.5);
    position: absolute;
    z-index: 10000;
    img{
        width: 200px;
        height: 200px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-80px,-100px);
        z-index: 1000;
    }
}
.el-submenu__title{
    margin-left: -50px;
}
