<template>
    <div class="b">
        <!-- <el-breadcrumb>
            <el-breadcrumb-item :to="{ path: '/list/welcome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>{{ uname }}</el-breadcrumb-item>
            <el-breadcrumb-item>{{ name }}</el-breadcrumb-item>
        </el-breadcrumb> -->

        <div class="bg">
            <!-- <el-input class="input" v-model="input" placeholder="请输入内容"></el-input> -->
            <div class="sou">
                <span class="span" v-show="flag" @click="qc()">X</span>
                <input type="text" class="input" @input="sr()" @keyup.enter="search()" v-model="sou" placeholder="请输入内容">
                <p class="el-icon-search" @click="search()"></p>
                <el-button type="primary" class="btn" @click="tj = true">添加用户</el-button>
            </div>

            <table border="1px" cellpadding='0' cellspacing='0' width='90%' height='30px'> 
                <tr>
                    <th>#</th>
                    <th>姓名</th>
                    <th>邮箱</th>
                    <th>电话</th>
                    <th>角色</th>
                    <th>状态</th>
                    <th>操作</th>
                </tr>
                <tr v-for="(item,key) in list" :key='key'>
                    <td>{{ item.id }}</td>
                    <td>{{ item.username }}</td>
                    <td>{{ item.email }}</td>
                    <td>{{ item.mobile }}</td>
                    <td>{{ item.role_name }}</td>
                    <td @click="qh(item)">
                        <el-switch 
                            v-model="item.mg_state"
                            active-color="#13ce66"
                            inactive-color="#DCDFE6">
                        </el-switch>
                    </td>
                    <td>
                        <!-- 修改 --><el-button type="primary" icon="el-icon-edit" @click="x(item)"></el-button>
                        <!-- 删除 --><el-button @click="del(key,item.id)" type="danger" icon="el-icon-delete" ></el-button>
                        <!-- 分配 -->
                        <el-tooltip content="分配权限" placement="top">
                           <el-button :enterable='false' type="warning" icon="el-icon-s-tools" @click='f(item)' ></el-button>
                        </el-tooltip>
                        
                    </td>
                </tr>
            </table>

             <div class="block">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagenum"
                :page-sizes="[1, 2, 3, 4]"
                :page-size='pagesize'
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>

            <!-- 添加用户 -->
            <el-dialog title="添加用户" :visible.sync="tj" width='30%'>
                <el-form>
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="用户名" prop="name">
                            <el-input v-model="ruleForm.name"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="密码" prop="pwd">
                            <el-input type='password' v-model="ruleForm.pwd"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="邮箱" prop="email">
                            <el-input v-model="ruleForm.email"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="电话" prop="phone">
                            <el-input v-model="ruleForm.phone"></el-input>
                        </el-form-item>
                    </el-form>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="tj = false">取 消</el-button>
                    <el-button type="primary" @click="add()">确 定</el-button>
                </div>
            </el-dialog>

            <!-- 编辑用户 -->
            <el-dialog title="编辑用户" :visible.sync="xg" width='30%'>
                <el-form>
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="用户名" prop="bjname">
                            <el-input disabled v-model="ruleForm.bjname"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="邮箱" prop="bjemail">
                            <el-input v-model="ruleForm.bjemail"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="电话" prop="bjphone">
                            <el-input v-model="ruleForm.bjphone"></el-input>
                        </el-form-item>
                    </el-form>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="xg = false">取 消</el-button>
                    <el-button type="primary" @click="gai()">确 定</el-button>
                </div>
            </el-dialog>

            <!-- 分配角色 -->
            <el-dialog title="分配角色" :visible.sync="fp" width="30%">
                <div class='fp'>
                    <span>当前的用户：<span>{{ ruleForm.uname }}</span></span>
                    <span>当前的角色：<span>{{ ruleForm.role }}</span></span>
                    <el-form>
                        <el-form-item label="分配新角色：">
                            <el-select v-model="ruleForm.xrole" placeholder="请选择">
                            <el-option :label="ruleForm.pu" :value="ruleForm.pu"></el-option>
                            <el-option :label="ruleForm.cs" :value="ruleForm.cs"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-form>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="fp = false">取 消</el-button>
                    <el-button type="primary" @click="fpjs()">确 定</el-button>
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        let checkTel = (rule, value, callback) => {
            let phone = /^1[345678]\d{9}$/
            if (!value) {
                return callback(new Error('手机号不能为空'));
            }

            if(phone.test(value)){
                return callback();
            }else{
                return callback(new Error('请输入正确的手机号'));
            }
        };
        return {
            uname:'',
            name:'',
            isCollapse: true,
            list:[],
            pagenum:1,
            pagesize:2,
            total:4,
            sou:'',
            flag:false,
            ruleForm: {
                name:'',
                pwd:'',
                email:'',
                phone:'',
                bjname:'',
                bjemail:'',
                bjphone:'',
                xrole:'',
                uname:'',
                role:'',
                pu:'普通用户',
                cs:'测试',
                xrole:'',
            },
            rules: {
                name: [
                    { required: true, message: '请输入用户名', trigger: ['blur', 'change'] },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: ['blur', 'change'] },
                ],
                pwd: [
                    { required: true, message: '请输入密码', trigger: ['blur', 'change'] },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: ['blur', 'change'] },
                ],
                email: [
                    { required: true, message: '请输入邮箱', trigger: 'blur' },
                    { type: 'email', message: '请输入正确的邮箱', trigger: ['blur', 'change'] },
                ],
                phone: [
                    { required: true, message: '请输入电话号', trigger: 'blur' },
                    { validator: checkTel, trigger: ['blur', 'change'] },
                ],
                bjname: [
                    { required: true, message: '请输入新的用户名', trigger: ['blur', 'change'] },
                ],
                bjemail: [
                    { required: true, message: '请输入新的邮箱', trigger: 'blur' },
                    { type: 'email', message: '请输入正确的邮箱', trigger: ['blur', 'change'] },
                ],
                bjphone: [
                    { required: true, message: '请输入新的电话号', trigger: 'blur' },
                    { validator: checkTel, trigger: ['blur', 'change'] },
                ],
            },
            tj:false,
            xg:false,
            fp:false,
            xlist:[],
            xfp:[],
        };
    },
    created() {

    },
    mounted() {
        this.name=this.$route.query.name
        this.uname=this.$route.query.uname
        this.fy()
    },
    methods: {
        handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        val=this.pagesize=val
        this.fy()
        
      },
        handleCurrentChange(val) {
            val=this.pagenum=val
            this.fy()
        },
        fy(){
            let obj={
                pagenum:this.pagenum,
                pagesize:this.pagesize
            }
            this.$http.get(`users?pagenum=${obj.pagenum}&pagesize=${obj.pagesize}`).then((res) => {
                console.log(res.data.data)
                this.list=res.data.data.users
                this.total=res.data.data.total
            })
        },
        del(k,id){
            this.$http.delete(`users/`+id).then((res) => {
                console.log(res)
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                    this.list.splice(k,1)
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    this.fy()
                    }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
            })
        },
        // 添加用户
        add(){
            let obj={
                name:this.ruleForm.name,
                pwd:this.ruleForm.pwd,
                email:this.ruleForm.email,
                dh:this.ruleForm.phone
            }
            this.$http.post(`users`,{username:obj.name,password:obj.pwd,email:obj.email,mobile:obj.dh}).then((res) => {
                console.log(res)
                this.$message({
                    type: 'success',
                    duration:2000,
                    message: '添加成功'
                });
                this.tj=false
                this.fy()
            })
        },
        x(item){
            this.xg=true
            this.xlist=item
            console.log(this.xlist)

            this.ruleForm.bjname=this.xlist.username
            this.ruleForm.bjemail=this.xlist.email
            this.ruleForm.bjphone=this.xlist.mobile
        },
        gai(){
            console.log(this.xlist.email)
            console.log(this.xlist.mobile)
            this.$http.put('users/'+this.xlist.id,{email:this.ruleForm.bjemail,mobile:this.ruleForm.bjphone}
            ).then((res) => {
                console.log(res)
                console.log(res.data.data)
                this.$message({
                    type: 'success',
                    duration:2000,
                    message: '更新成功'
                });
                this.xg=false
                this.fy()
            })
        },
        fpjs(){
            console.log(this.xfp.id)
            this.$http.put(`users/${this.xfp.id}/role`,{
                rid:1
            }).then((res) => {
                console.log(res)
                this.xfp.role_name=this.ruleForm.xrole
                this.$message({
                    type: 'success',
                    duration:2000,
                    message: '更新角色成功'
                });
                this.fp = false
                this.fy()
            })
        },
        // 分配角色
        f(item){
            this.fp = true
            this.xfp = item
            console.log(this.xfp)
            this.ruleForm.uname=this.xfp.username
            this.ruleForm.role=this.xfp.role_name
            // item.role_name=this.ruleForm.xrole
        },
        // 模糊搜索
        search(){
            let obj={
                pagenum:this.pagenum,
                pagesize:this.pagesize
            }
            this.$http.get(`users?pagenum=${obj.pagenum}&pagesize=${obj.pagesize}`).then((res) => {
                console.log(res.data.data)
                let obj= res.data.data.users.filter((i,k) => {
                    return i.username.indexOf(this.sou) != -1
                })
                console.log(obj)

                this.list=obj
            })
        },
        // 判断输入的时候显示删除按钮
        sr(){
            // 如果不为空就显示X号
            if(this.sou!=''){
                this.flag=true
            }else{
                this.flag=false
            }
        },
        // 清空input搜索框的内容
        qc(){
            this.sou=''
            this.flag=false
        },
        qh(item){
            console.log(item.mg_state)
            this.$http.put(`users/`+item.id+'/state/'+item.mg_state).then((res) => {
                console.log(res)
                this.$message({
                    type: 'success',
                    duration:2000,
                    message: '修改状态成功'
                });
                this.fy()
            })
        }
    }
};
</script>

<style scoped lang='scss'>
.b{
    width: 100%;
    height: 100%;
}
.bg{
    width: 99%;
    background: white;
    margin-top: 20px;
    table{
        margin: 50px auto;
        // border: 1px solid #EBEEF5;
        border-color: #EBEEF5;
        tr{
            line-height: 40px;
        }
    }
}
.el-button{
    width: 50px;
    height: 30px;
    text-indent: -1px;
    margin: 7px 0px;
    padding: 9px 20px;
}
.block{
    height: 60px;
    margin-top: 30px;
    text-align: left;
    margin-left: 67px;
}
.sou{
    height: 130px;
    position: relative;
    .input{
        width: 350px;
        height: 43px;
        margin-top: 6%;
        text-align: left;
        float: left;
        margin-left: 50px;
        border: 1px solid #ddd;
        border-radius: 5px 0px 0px 5px;
        background: #F5F7FA;
        text-indent: 14px;

    }
    .el-icon-search{
        width: 70px;
        height: 43px;
        line-height: 43px;
        float: left;
        margin-left: 0px;
        margin-top: 6%;
        font-size: 18px;
        border: 1px solid #ddd;
        border-left: none;
        border-radius: 0px 5px 5px 0px;
        background: #F5F7FA;
    }
    .btn{
        width: 100px;
        height: 39px;
        float: left;
        margin-left: 27px;
        margin-top: 6%;
    }
    .span{
        width: 20px;
        height: 20px;
        display: block;
        // border: 1px solid #ddd;
        // border-radius: 50px;
        position: absolute;
        top: 1px;
        left: 370px;
    }
}
.demo-ruleForm{
    width: 80%;
}
.dialog-footer{
    .el-button{
        width: 90px;
        height: 40px;
    }
}
.fp{
    width: 318px;
    margin: 0 auto;
    font-size: 13px;
    span{
        display: inline-block;
        width: 50%;
    }
}
.load{
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.5);
    // background: white;
    position: absolute;
    z-index: 10000;
    img{
        width: 200px;
        height: 200px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-90px,-100px);
        z-index: 1000;
    }
}
</style>