import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import List from '../views/List.vue'
import Users from '../user/Users.vue'
import welcome from '../user/welcome.vue'
import Rights from '../user/Rights.vue'
import Roles from '../user/Roles.vue'
import Goods from '../user/Goods.vue'
import Params from '../user/Params.vue'
import Categories from '../user/Categories.vue'
import Orders from '../user/Orders.vue'
import Reports from '../user/Reports.vue'
import addgoods from '../user/addgoods.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/list',
    name: 'List',
    component: List,
    beforeEnter(to,from,next){
      if(window.sessionStorage.getItem('token')){
        next()
      }else{
        alert('请先登录')
        next('/login')
      }
    },
    children:[
      {
        path: 'users',
        name: 'Users',
        component: function () {
          return import(/* webpackChunkName: "about" */ '../user/Users.vue')
        }
        // component: Users
      },
      {
        path: 'welcome',
        name: 'welcome',
        component: welcome
      },
      {
        path: 'rights',
        name: 'Rights',
        component: function () {
          return import(/* webpackChunkName: "about" */ '../user/Rights.vue')
        }
        // component: Rights,
      },
      {
        path: 'roles',
        name: 'Roles',
        component: function () {
          return import(/* webpackChunkName: "about" */ '../user/Roles.vue')
        }
        // component: Roles,
      },
      {
        path: 'goods',
        name: 'Goods',
        component: Goods,
      },
      {
        path: 'params',
        name: 'Params',
        component: Params,
      },
      {
        path: 'categories',
        name: 'Categories',
        component: Categories,
      },
      {
        path: 'orders',
        name: 'Orders',
        component: Orders,
      },
      {
        path: 'reports',
        name: 'Reports',
        component: Reports,
      },
      {
        path: 'addgoods',
        name: 'addgoods',
        component: addgoods,
      },
      {
        path: '/list',
        redirect: '/list/welcome'
      },
    ]
  },
  // {
  //   path: '/:id',
  //   redirect:'/'
  // },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/About.vue')
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
